import React, { Component } from 'react';
import '../station/Station.css';
import {Link} from 'react-router'
import './BusList.css'

function HelloComponent(props,/*context*/) {
    return <div className="chargeBusList">
        <div className="chargeBusPading">
            <p className="topLeftTitle">
                <span>{props.time}</span>
                <span> {props.line_name}</span>
                <span className="topRightTitle floatRight"></span>
            </p>
        </div>
        <p className="topLine"></p>
        <div className="clearBoth addressBottom chargeBusPading">

            <div className="floatLeft">
                <div className="lv_out">
                </div>
                <span className="clearBoth fromAddressAlign">{props.fromAddress}</span>
                <span className="clearBoth toAddressAlign">{props.toAddress}</span>
            </div >

            <div className="floatRight">
                <span className="priceAlign">0.1</span>
                <span className="minsAlign">50mins</span>
            </div>
        </div>
    </div>
}

export default  class BusList extends Component {
    render() {
        return (
            <div className="list_bg">

             <Link to="/bus_details">
                    <HelloComponent line_name="1" time = "07:30" fromAddress = "TMT" toAddress="6()"/>
             </Link>

             <Link to="/bus_details">
                    <HelloComponent line_name="2" time = "08:30" fromAddress="" toAddress="TMT" />
             </Link>

             <Link to="/bus_details">
                    <HelloComponent line_name="3" time = "09:30" fromAddress="863" toAddress="1" />
             </Link>

             <Link to="/bus_details">
                    <HelloComponent line_name="4" time="10:30" fromAddress="" toAddress=""/>
             </Link>

                <div className="bg-bottom"></div>

            </div>
        );
    }
}


