import React, { Component } from 'react';
import './warning.css'
import SearchBar from 'antd-mobile/lib/search-bar'
import Button from 'antd-mobile/lib/button'
import WingBlank from 'antd-mobile/lib/wing-blank'
import Tabs from 'antd-mobile/lib/tabs'
import WhiteSpace from 'antd-mobile/lib/white-space'
import {fetchOpsWarningCount} from '../../service/getData'

const TabPane = Tabs.TabPane;

var WarningList = React.createClass({
   proptypes () {

   },
    render: function () {
        return <div className="content_bg cf">
            <div>
                <p className="content_text fl"> : 111</p>
                <div className="fr">
                    <span className="content_text"> :</span>
                    <span className="content_level"> 1 </span>
                </div>
            </div>
            <p className="content_text"> :  </p>
            <p className="content_text"> :  </p>
            <p className="content_text"> :  </p>
            <p className="content_text"> : 863 </p>
            <div>
                <span className="content_text"> ID: 2358656885 </span>
                <span className="content_copy">  </span>
            </div>
        </div>
    }
});

export default class WarningHost extends Component {

    constructor(props) {
        super(props);
        this.state = {
            datas: [], // 
            warning_count: '',
            warning_today: ''
        };
    }

    componentWillMount() {
        this.fetchWarningCount()
    }

    fetchWarningCount() {
        fetchOpsWarningCount().then(res => {
            this.setState({
                warning_count: res.waiting_alarm,
                warning_today: res.today_alarm
            })
        })
    }

    callback(key) {
        console.log('onChange', key);
    }
    handleTabClick(key) {
        console.log('onTabClick', key);
    }

    render() {
        return (
            <div>
                <SearchBar placeholder="ID"
                           showCancelButton={false}
                />

                <div >
                    <Button  className="btn fl btn_left" type="primary" onClick={e => console.log(e)}>
                        :{this.state.warning_count} 
                    </Button>

                    <Button className="btn fr btn_right" type="primary" onClick={e => console.log(e)}>
                        :{this.state.warning_today} 
                    </Button>
                </div>
                <Tabs className='cf' defaultActiveKey="2" animated={false} onChange={this.callback} onTabClick={this.handleTabClick}>
                    <TabPane tab="" key="1">
                        <div className="padding_10 "
                            style={{ height: '15rem', backgroundColor: '#f5f5f9' }}>

                          <WarningList/>
                            <WarningList/>
                            <WarningList/>

                        </div>
                    </TabPane>
                    <TabPane tab="" key="2">
                        <div className="padding_10"
                            style={{ height: '15rem', backgroundColor: '#f5f5f9' }}>

                            <WarningList/>
                            <WarningList/>
                            <WarningList/>
                            <WarningList/>
                        </div>
                    </TabPane>
                </Tabs>
                <WhiteSpace />
            </div>
        )
    }
}


